.block3d-container {
  position: relative;
  width: 100%;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  perspective: 1000px;
}

.block3d {
  width: 300px;
  height: 300px;
  border-radius: 20px;
  position: relative;
  cursor: pointer;
  transform-style: preserve-3d;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.3),
    inset 0 2px 8px rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  user-select: none;
  border: 2px solid rgba(255, 255, 255, 0.1);
}

/* Size variations */
.block3d.size-1x2 {
  width: 400px;
  height: 200px;
}

.block3d.size-2x1 {
  width: 200px;
  height: 400px;
}

/* Rainbow gradient effect */
.block3d.rainbow {
  background: linear-gradient(45deg, 
    #000000 0%, 
    #FF0000 16.66%, 
    #0066CC 33.33%, 
    #800080 50%, 
    #FFA500 66.66%, 
    #FFC0CB 83.33%, 
    #008000 100%);
  animation: rainbowShift 3s ease-in-out infinite;
}

@keyframes rainbowShift {
  0%, 100% { 
    filter: hue-rotate(0deg) brightness(1);
  }
  50% { 
    filter: hue-rotate(30deg) brightness(1.1);
  }
}

/* Block content */
.block-content {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px;
  position: relative;
  z-index: 2;
}

.block-side {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100%;
}

.block-text {
  color: white;
  font-weight: 700;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
  margin-bottom: 10px;
  line-height: 1.1;
  font-family: 'PingFang SC', 'Noto Sans SC', 'Hiragino Sans GB', 'Microsoft YaHei', sans-serif;
}

.block-emoji {
  font-size: 2rem;
  margin: 10px 0;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

.block-subtext {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.2rem;
  font-weight: 500;
  margin-top: 8px;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.5);
}

/* Size indicator */
.size-indicator {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 600;
  backdrop-filter: blur(10px);
}

/* Instruction overlay */
.instruction-overlay {
  position: absolute;
  top: -80px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}

.instruction-bubble {
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 12px 20px;
  border-radius: 20px;
  font-size: 1rem;
  font-weight: 500;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  position: relative;
  max-width: 300px;
  text-align: center;
}

.instruction-bubble::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 8px solid transparent;
  border-top-color: rgba(0, 0, 0, 0.8);
}

/* Color selection options */
.color-options {
  position: absolute;
  top: 50%;
  right: -100px;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 15px;
  z-index: 5;
}

.color-option {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  cursor: pointer;
  border: 3px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  transition: all 0.2s ease;
}

.color-option:hover {
  border-color: rgba(255, 255, 255, 0.6);
  transform: scale(1.1);
}

/* Swipe indicators */
.swipe-indicator {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: white;
  font-weight: 600;
  font-size: 1rem;
  padding: 8px 16px;
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  z-index: 1;
  pointer-events: none;
  transition: all 0.3s ease;
}

.swipe-indicator.left {
  left: -150px;
  background: rgba(255, 107, 107, 0.8);
}

.swipe-indicator.right {
  right: -150px;
  background: rgba(107, 203, 119, 0.8);
}

/* Transparent state */
.block3d[style*="transparent"] {
  background: rgba(255, 255, 255, 0.1) !important;
  border: 2px dashed rgba(255, 255, 255, 0.4);
}

.block3d[style*="transparent"] .block-text {
  color: rgba(255, 255, 255, 0.6);
}

/* Hover and interaction states */
.block3d:hover {
  transform: translateY(-5px) rotateX(5deg);
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.4),
    inset 0 2px 8px rgba(255, 255, 255, 0.2);
}

.block3d:active {
  transform: translateY(-2px) scale(0.98);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .block3d {
    width: 280px;
    height: 280px;
  }
  
  .block3d.size-1x2 {
    width: 360px;
    height: 180px;
  }
  
  .block-text {
    font-size: 2.5rem !important;
  }
  
  .color-options {
    right: -80px;
  }
  
  .color-option {
    width: 50px;
    height: 50px;
  }
  
  .swipe-indicator {
    font-size: 0.9rem;
    padding: 6px 12px;
  }
  
  .swipe-indicator.left {
    left: -120px;
  }
  
  .swipe-indicator.right {
    right: -120px;
  }
}

@media (max-width: 480px) {
  .block3d {
    width: 250px;
    height: 250px;
  }
  
  .block3d.size-1x2 {
    width: 320px;
    height: 160px;
  }
  
  .block-text {
    font-size: 2rem !important;
  }
  
  .instruction-overlay {
    top: -60px;
  }
  
  .instruction-bubble {
    font-size: 0.9rem;
    padding: 10px 16px;
    max-width: 250px;
  }
} 