.assessment-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #ff6b9d 100%);
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

/* Animated background */
.assessment-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(255, 107, 157, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(168, 85, 247, 0.4) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(34, 197, 94, 0.3) 0%, transparent 50%);
  animation: float 20s ease-in-out infinite;
  z-index: 1;
}

@keyframes float {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg); 
    filter: hue-rotate(0deg);
  }
  50% { 
    transform: translateY(-20px) rotate(5deg); 
    filter: hue-rotate(90deg);
  }
}

/* Header */
.assessment-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  z-index: 2;
  position: relative;
}

.phase-indicator {
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 12px 24px;
}

.phase-label {
  color: white;
  font-weight: 600;
  font-size: 1.1rem;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

.progress-counter {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 15px;
  padding: 10px 20px;
  color: white;
  font-weight: 700;
  font-size: 1.2rem;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

/* Progress Bar */
.assessment-progress {
  height: 6px;
  background: rgba(255, 255, 255, 0.2);
  margin: 0 30px 20px;
  border-radius: 3px;
  overflow: hidden;
  z-index: 2;
  position: relative;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #51cf66, #69db7c);
  border-radius: 3px;
  transition: width 0.5s ease;
  box-shadow: 0 0 10px rgba(81, 207, 102, 0.5);
}

/* Instructions */
.assessment-instructions {
  text-align: center;
  padding: 20px 30px;
  z-index: 2;
  position: relative;
}

.assessment-instructions h3 {
  color: white;
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 20px;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.instruction-options {
  display: flex;
  justify-content: center;
  gap: 30px;
  max-width: 800px;
  margin: 0 auto;
}

.option {
  display: flex;
  align-items: center;
  gap: 10px;
  background: rgba(255, 255, 255, 0.1);
  padding: 12px 20px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  font-weight: 500;
  transition: all 0.3s ease;
}

.option:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
}

.option-icon {
  font-size: 1.2rem;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

/* Assessment Content */
.assessment-content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  z-index: 2;
  position: relative;
}

.block-wrapper {
  width: 100%;
  max-width: 400px;
}

/* Score Display */
.score-display {
  display: flex;
  justify-content: center;
  gap: 30px;
  padding: 20px 30px;
  z-index: 2;
  position: relative;
}

.score-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  background: rgba(255, 255, 255, 0.1);
  padding: 15px 25px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.score-label {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-weight: 500;
}

.score-value {
  color: white;
  font-size: 1.5rem;
  font-weight: 700;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

/* Transition Screen */
.transition-screen {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  position: relative;
}

.transition-content {
  text-align: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  padding: 50px;
  max-width: 500px;
  margin: 0 20px;
}

.transition-icon {
  font-size: 4rem;
  margin-bottom: 30px;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.3));
}

.transition-content h2 {
  color: white;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.transition-content p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.2rem;
  margin-bottom: 15px;
  line-height: 1.5;
}

.transition-progress {
  margin-top: 30px;
}

.progress-circle {
  width: 80px;
  height: 80px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top-color: white;
  border-radius: 50%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: spin 2s linear infinite;
}

.progress-circle span {
  color: white;
  font-weight: 600;
  font-size: 0.9rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Completion Screen */
.completion-screen {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  z-index: 2;
  position: relative;
  padding: 20px;
}

.completion-icon {
  font-size: 5rem;
  margin-bottom: 30px;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.3));
}

.completion-screen h2 {
  color: white;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.completion-screen p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.3rem;
  font-weight: 500;
}

/* Loading State */
.loading-text {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  font-weight: 600;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  z-index: 2;
  position: relative;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .assessment-header {
    padding: 15px 20px;
    flex-direction: column;
    gap: 15px;
  }
  
  .assessment-progress {
    margin: 0 20px 15px;
  }
  
  .assessment-instructions {
    padding: 15px 20px;
  }
  
  .assessment-instructions h3 {
    font-size: 1.5rem;
  }
  
  .instruction-options {
    flex-direction: column;
    gap: 15px;
    align-items: center;
  }
  
  .option {
    min-width: 250px;
    justify-content: center;
  }
  
  .score-display {
    gap: 20px;
    padding: 15px 20px;
  }
  
  .transition-content {
    padding: 40px 30px;
    margin: 0 15px;
  }
  
  .transition-content h2 {
    font-size: 2rem;
  }
  
  .completion-screen h2 {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .assessment-header {
    padding: 10px 15px;
  }
  
  .phase-label {
    font-size: 1rem;
  }
  
  .progress-counter {
    font-size: 1rem;
    padding: 8px 16px;
  }
  
  .assessment-instructions h3 {
    font-size: 1.3rem;
  }
  
  .option {
    min-width: 200px;
    padding: 10px 16px;
  }
  
  .option-icon {
    font-size: 1rem;
  }
  
  .score-display {
    flex-direction: column;
    gap: 15px;
    align-items: center;
  }
  
  .transition-content {
    padding: 30px 20px;
  }
  
  .transition-icon,
  .completion-icon {
    font-size: 3rem;
  }
  
  .transition-content h2,
  .completion-screen h2 {
    font-size: 1.8rem;
  }
} 