.study-mode-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #ff6b9d 100%);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

/* Animated background */
.study-mode-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(255, 107, 157, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(168, 85, 247, 0.4) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(34, 197, 94, 0.3) 0%, transparent 50%);
  animation: float 20s ease-in-out infinite;
  z-index: 1;
}

@keyframes float {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg); 
    filter: hue-rotate(0deg);
  }
  50% { 
    transform: translateY(-20px) rotate(5deg); 
    filter: hue-rotate(90deg);
  }
}

/* Loading State */
.study-loading {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #ff6b9d 100%);
}

.loading-text {
  color: white;
  font-size: 1.5rem;
  font-weight: 600;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

/* Navigation Bar */
.study-nav {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 15px 20px;
  z-index: 10;
  position: relative;
}

.nav-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.nav-section {
  display: flex;
  align-items: center;
  gap: 15px;
}

.nav-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.nav-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-1px);
}

.nav-btn.active {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.4);
}

.progress-info {
  color: white;
  font-weight: 600;
  font-size: 1.1rem;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

/* Welcome Back Section */
.welcome-back {
  text-align: center;
  padding: 40px 20px 20px;
  z-index: 2;
  position: relative;
}

.welcome-back h2 {
  color: white;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.welcome-back p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.2rem;
  font-weight: 500;
}

/* Block Study Area */
.block-study-area {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  z-index: 2;
  position: relative;
}

/* Study Instructions */
.study-instructions {
  padding: 20px;
  z-index: 2;
  position: relative;
}

.instruction-row {
  display: flex;
  justify-content: center;
  gap: 30px;
  max-width: 800px;
  margin: 0 auto;
}

.instruction-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  background: rgba(255, 255, 255, 0.1);
  padding: 15px 20px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.instruction-item:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
}

.instruction-icon {
  font-size: 1.5rem;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

.instruction-text {
  color: white;
  font-size: 0.9rem;
  font-weight: 500;
  text-align: center;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
}

/* Navigation Toggle */
.nav-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-toggle:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

/* Session Progress */
.session-progress {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: rgba(255, 255, 255, 0.2);
  z-index: 10;
}

.progress-bar {
  height: 100%;
  background: linear-gradient(90deg, #ff6b9d, #ffd93d);
  transition: width 0.5s ease;
  box-shadow: 0 0 10px rgba(255, 107, 157, 0.5);
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .nav-content {
    flex-direction: column;
    gap: 15px;
  }
  
  .nav-section {
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .welcome-back h2 {
    font-size: 2rem;
  }
  
  .welcome-back p {
    font-size: 1rem;
  }
  
  .instruction-row {
    flex-direction: column;
    gap: 15px;
    align-items: center;
  }
  
  .instruction-item {
    flex-direction: row;
    gap: 12px;
    min-width: 200px;
  }
  
  .nav-toggle {
    top: 15px;
    right: 15px;
    width: 45px;
    height: 45px;
  }
}

@media (max-width: 480px) {
  .welcome-back {
    padding: 30px 15px 15px;
  }
  
  .welcome-back h2 {
    font-size: 1.8rem;
  }
  
  .nav-btn {
    padding: 8px 15px;
    font-size: 0.9rem;
  }
  
  .instruction-item {
    padding: 12px 15px;
    min-width: 180px;
  }
  
  .instruction-text {
    font-size: 0.8rem;
  }
} 