.onboarding-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #ff6b9d 100%);
  padding: 20px;
  position: relative;
  overflow: hidden;
}

/* Animated background */
.onboarding-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(255, 107, 157, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(168, 85, 247, 0.4) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(34, 197, 94, 0.3) 0%, transparent 50%);
  animation: float 20s ease-in-out infinite;
  z-index: 1;
}

@keyframes float {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg); 
    filter: hue-rotate(0deg);
  }
  50% { 
    transform: translateY(-20px) rotate(5deg); 
    filter: hue-rotate(90deg);
  }
}

/* Welcome Screen Styles */
.welcome-screen {
  text-align: center;
  max-width: 600px;
  z-index: 2;
  position: relative;
}

.welcome-title {
  color: white;
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 20px;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  background: linear-gradient(45deg, #ff6b9d, #ffd93d, #6bcf7f, #4d94ff);
  background-size: 300% 300%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 3s ease-in-out infinite;
}

.welcome-description {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.3rem;
  font-weight: 500;
  margin-bottom: 40px;
  line-height: 1.6;
}

@keyframes gradientShift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.demo-block-container {
  margin: 40px 0;
  display: flex;
  justify-content: center;
}

.get-started-btn {
  background: linear-gradient(45deg, #ff6b9d, #ffd93d);
  color: white;
  border: none;
  padding: 18px 40px;
  font-size: 1.3rem;
  font-weight: 600;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}

.get-started-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.3);
}

/* Tutorial Screen Styles */
.tutorial-screen {
  text-align: center;
  max-width: 700px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  padding: 40px;
  z-index: 2;
  position: relative;
}

.tutorial-title {
  color: white;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.tutorial-description {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.2rem;
  margin-bottom: 30px;
}

.tutorial-features {
  list-style: none;
  padding: 0;
  margin: 30px 0;
  text-align: left;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.tutorial-features li {
  color: white;
  font-size: 1.1rem;
  margin-bottom: 15px;
  padding: 12px 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.continue-btn {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 15px 35px;
  font-size: 1.2rem;
  font-weight: 600;
  border-radius: 25px;
  cursor: pointer;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  margin-top: 20px;
}

.continue-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.5);
  transform: translateY(-2px);
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .welcome-title {
    font-size: 2.8rem;
  }
  
  .welcome-description {
    font-size: 1.1rem;
    margin-bottom: 30px;
  }
  
  .get-started-btn {
    padding: 15px 30px;
    font-size: 1.1rem;
  }
  
  .tutorial-screen {
    padding: 30px 20px;
    margin: 0 10px;
  }
  
  .tutorial-title {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .welcome-title {
    font-size: 2.2rem;
  }
  
  .welcome-description {
    font-size: 1rem;
    padding: 0 10px;
  }
  
  .get-started-btn {
    padding: 12px 25px;
    font-size: 1rem;
  }
  
  .tutorial-features li {
    font-size: 1rem;
    padding: 10px 15px;
  }
} 