.landing-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

/* Loading screen styles */
.landing-loading {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #ff6b9d 100%);
  position: relative;
  overflow: hidden;
}

.loading-logo {
  text-align: center;
  z-index: 2;
}

.logo-blocks {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 30px;
}

.logo-block {
  width: 60px;
  height: 60px;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.logo-block.block-1 {
  background: linear-gradient(45deg, #FF0000, #FF4444);
}

.logo-block.block-2 {
  background: linear-gradient(45deg, #000000, #333333);
}

.logo-block.block-3 {
  background: linear-gradient(45deg, #0066CC, #4488DD);
}

.loading-title {
  color: white;
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 15px;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  background: linear-gradient(45deg, #ff6b9d, #ffd93d, #6bcf7f, #4d94ff);
  background-size: 300% 300%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 3s ease-in-out infinite;
}

.loading-subtitle {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.2rem;
  font-weight: 500;
  margin: 0;
}

@keyframes gradientShift {
  0%, 100% { 
    background-position: 0% 50%; 
  }
  50% { 
    background-position: 100% 50%; 
  }
}

/* Animated background elements */
.landing-loading::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(255, 107, 157, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(168, 85, 247, 0.4) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(34, 197, 94, 0.3) 0%, transparent 50%);
  animation: float 20s ease-in-out infinite;
  z-index: 1;
}

@keyframes float {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg); 
    filter: hue-rotate(0deg);
  }
  50% { 
    transform: translateY(-20px) rotate(5deg); 
    filter: hue-rotate(90deg);
  }
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .logo-blocks {
    gap: 15px;
  }
  
  .logo-block {
    width: 50px;
    height: 50px;
  }
  
  .loading-title {
    font-size: 2.8rem;
  }
  
  .loading-subtitle {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .logo-blocks {
    gap: 12px;
  }
  
  .logo-block {
    width: 45px;
    height: 45px;
  }
  
  .loading-title {
    font-size: 2.2rem;
  }
  
  .loading-subtitle {
    font-size: 0.9rem;
    padding: 0 20px;
  }
} 