.tutorial-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #ff6b9d 100%);
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

/* Animated background */
.tutorial-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(255, 107, 157, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(168, 85, 247, 0.4) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(34, 197, 94, 0.3) 0%, transparent 50%);
  animation: float 20s ease-in-out infinite;
  z-index: 1;
}

@keyframes float {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg); 
    filter: hue-rotate(0deg);
  }
  50% { 
    transform: translateY(-20px) rotate(5deg); 
    filter: hue-rotate(90deg);
  }
}

/* Progress Indicator */
.tutorial-progress {
  padding: 30px 20px 20px;
  text-align: center;
  z-index: 2;
  position: relative;
}

.progress-steps {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.progress-step {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.2rem;
  border: 3px solid rgba(255, 255, 255, 0.3);
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.7);
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.progress-step.active {
  border-color: rgba(255, 255, 255, 0.8);
  background: rgba(255, 255, 255, 0.2);
  color: white;
  transform: scale(1.1);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.progress-step.completed {
  border-color: #51cf66;
  background: #51cf66;
  color: white;
  box-shadow: 0 8px 25px rgba(81, 207, 102, 0.3);
}

.progress-title {
  color: white;
  font-size: 1.5rem;
  font-weight: 600;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

/* Tutorial Content */
.tutorial-content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  z-index: 2;
  position: relative;
}

.block-container {
  position: relative;
  width: 100%;
  max-width: 500px;
}

/* Arrow Guide */
.arrow-guide {
  position: absolute;
  font-size: 4rem;
  color: white;
  font-weight: bold;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  z-index: 5;
  pointer-events: none;
  animation: pulse 1.5s ease-in-out infinite;
}

.arrow-guide.right {
  right: -100px;
  top: 50%;
  transform: translateY(-50%);
}

.arrow-guide.left {
  left: -100px;
  top: 50%;
  transform: translateY(-50%);
}

@keyframes pulse {
  0%, 100% { 
    transform: translateY(-50%) scale(1); 
    opacity: 0.8;
  }
  50% { 
    transform: translateY(-50%) scale(1.2); 
    opacity: 1;
  }
}

/* Success Animation */
.success-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.success-content {
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(20px);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 25px;
  padding: 40px 60px;
  text-align: center;
  color: white;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.success-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  animation: bounce 0.6s ease-in-out;
}

.success-text {
  font-size: 2rem;
  font-weight: 700;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-20px);
  }
  60% {
    transform: translateY(-10px);
  }
}

/* Skip Tutorial Button */
.skip-tutorial {
  position: fixed;
  top: 20px;
  right: 20px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  padding: 12px 24px;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 500;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  z-index: 10;
}

.skip-tutorial:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .tutorial-progress {
    padding: 20px 15px 15px;
  }
  
  .progress-steps {
    gap: 15px;
  }
  
  .progress-step {
    width: 45px;
    height: 45px;
    font-size: 1rem;
  }
  
  .progress-title {
    font-size: 1.3rem;
  }
  
  .arrow-guide {
    font-size: 3rem;
  }
  
  .arrow-guide.right {
    right: -80px;
  }
  
  .arrow-guide.left {
    left: -80px;
  }
  
  .success-content {
    padding: 30px 40px;
    margin: 0 20px;
  }
  
  .success-icon {
    font-size: 3rem;
    margin-bottom: 15px;
  }
  
  .success-text {
    font-size: 1.5rem;
  }
  
  .skip-tutorial {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .progress-steps {
    gap: 10px;
  }
  
  .progress-step {
    width: 40px;
    height: 40px;
    font-size: 0.9rem;
  }
  
  .progress-title {
    font-size: 1.1rem;
    padding: 0 10px;
  }
  
  .arrow-guide {
    font-size: 2.5rem;
  }
  
  .arrow-guide.right {
    right: -60px;
  }
  
  .arrow-guide.left {
    left: -60px;
  }
  
  .success-content {
    padding: 25px 30px;
  }
  
  .success-icon {
    font-size: 2.5rem;
  }
  
  .success-text {
    font-size: 1.3rem;
  }
} 